import request from '@/utils/request'
// 会员记录
userVipRecordList
export function userVipRecordList(data) {
  return request({
    url: `/sys/business/vipConfig/userVipRecordList`,
    method: 'post',
    data
  })
}
// 会员中心数据
export function getVIpConfig(id) {
  return request({
    url: `/sys/business/vipConfig/getVIpConfig?shopId=${id}`,
    method: 'post',
  })
}
// 开启/关闭会员功能
export function openVip(data) {
  return request({
    url: `/sys/business/vipConfig/openVip`,
    method: 'post',
    data
  })
}
// 会员信息初始化
export function addVIpConfig(data) {
  return request({
    url: `/sys/business/vipConfig/addVIpConfig`,
    method: 'post',
    data
  })
}
// 会员中心调取优惠券列表
export function getCouponsList(data) {
  return request({
    url: `/sys/business/vipConfig/getCouponsList`,
    method: 'post',
    data
  })
}