<template>
  <div class="pageContent">
    <el-card class="filter-container" shadow="never">
      <div class="flex-row-between">
        <div>
          <div class="h3">会员功能</div>
          <div class="h3word">启用会员功能后，买家可以拥有会员身份及权益，帮助老用户活跃，提高复购率。</div>
        </div>
        <div>
          <el-switch
            v-model="isOpenVip"
            active-color="#13ce66"
            active-value="1"
            inactive-value="0"
            inactive-color="#ff4949"
            @change="changeStatus">
          </el-switch>
        </div>
      </div>
    </el-card>
    <el-card class="filter-container mt10" shadow="never">
      <div class="pageHead">基本信息</div>
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="名称：" required>
          <el-input style="width:400px;" maxlength="10" show-word-limit v-model="form.name"></el-input>
        </el-form-item>
        <el-form-item label="开通方式：" required>
          <el-radio-group v-model="form.openType">
            <el-radio label="1">免费开通</el-radio>
            <el-radio label="2">付费开通</el-radio>
          </el-radio-group>
        </el-form-item>
        <el-form-item v-if="form.openType == 1">
          <div class="flex-row-start mt10">
            <div>开通时起</div>
            <el-input style="margin:0 10px;width:80px;" v-model="rules1[0].effectiveTime"></el-input>
            <el-select style="margin-right:10px;width:120px;" v-model="rules1[0].unit">
              <el-option label="月" value="M"></el-option>
              <el-option label="年" value="Y"></el-option>
            </el-select>
            <div>内有效</div>
          </div>
        </el-form-item>
        <el-form-item v-if="form.openType == 2">
          <div class="flex-row-start mt10" v-for="(item,index) in rules2" :key="index">
            <div>开通时起</div>
            <el-input style="margin:0 10px;width:80px;" v-model="item.effectiveTime"></el-input>
            <el-select style="margin-right:10px;width:120px;" v-model="item.unit">
              <el-option label="月" value="M"></el-option>
              <el-option label="年" value="Y"></el-option>
            </el-select>
            <div>内有效</div>
            <div style="margin-left:20px;">支付</div>
            <el-input-number style="margin:0 10px;width:80px;" v-model="item.amount" :precision="2" :min="0.01" :controls="false"></el-input-number>
            <div>元</div>
            <el-button @click="deleteRule(index)" style="margin-left:40px;" type="text" :disabled="index == 0">删除</el-button>
          </div>
          <div class="mt10">
            <el-button :disabled="form.rules.length >= 5" @click="addRule">添加规则</el-button>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
    <el-card class="filter-container mt10" shadow="never">
      <div class="pageHead">权益设置</div>
      <el-form ref="form" :model="form" label-width="140px">
        <el-form-item label="权益：" required>
          <div><el-button @click="showListFun">选择权益</el-button></div>
          <div class="mt10" v-if="equityList.length > 0">
            <el-table :data="equityList" style="width: 100%">
              <el-table-column label="图标">
                <template slot-scope="scope">
                  <el-image style="width: 50px; height: 50px" :src="scope.row.equityUrl" fit="fill"></el-image>
                </template>
              </el-table-column>
              <el-table-column prop="equityName" label="名称"></el-table-column>
              <el-table-column prop="comment" label="描述"></el-table-column>
              <el-table-column label="操作">
                <template slot-scope="scope">
                  <el-button @click="deleteEq(scope.$index)" type="text" size="small">删除</el-button>
                </template>
              </el-table-column>
            </el-table>
          </div>
        </el-form-item>
        <el-form-item label="赠送礼包：" required>
          <el-radio-group v-model="form.isOpenGift">
            <el-radio label="1">无礼包</el-radio>
            <el-radio label="2">有礼包</el-radio>
          </el-radio-group>
          <div v-if="form.isOpenGift == 2" class="mt10">
            <div class="flex-row-start">
              <div>每</div>
              <el-select style="margin:0 10px;width:120px;" v-model="form.openRules">
                <el-option label="月" value="M"></el-option>
                <el-option label="年" value="Y"></el-option>
              </el-select>
              <div>定时发放</div>
            </div>
            <div class="infoFont">礼包在设定周期进行定时发放</div>
            <div class="mt10">
              <el-checkbox v-model="form.sendCoupon" disabled>送优惠券</el-checkbox>
            </div>
            <div class="mt10">
              <el-button type="primary" icon="el-icon-circle-plus-outline" @click="showCouponList = true">送优惠券</el-button>
            </div>
            <div class="mt10" v-if="couponList.length > 0">
              <el-table :data="couponList" style="width: 100%">
                <el-table-column prop="couponName" label="优惠券名称" ></el-table-column>
                <el-table-column prop="content" label="优惠内容" ></el-table-column>
                <el-table-column label="每人限领次数" >
                  <template slot-scope="scope">{{ scope.row.limitNumber == 0 ? '不限制' : scope.row.limitNumber }}</template>
                </el-table-column>
                <el-table-column label="定时赠送券数" >
                  <template slot-scope="scope">
                    <el-input-number v-model="scope.row.couponsCount" :min="0" :controls="false" step-strictly></el-input-number>
                  </template>
                </el-table-column>
                <el-table-column label="操作" width="100">
                  <template slot-scope="scope">
                    <el-button type="text" size="small" @click="deleteCoupon(scope.$index)">删除</el-button>
                  </template>
                </el-table-column>
              </el-table>
            </div>
          </div>
        </el-form-item>
      </el-form>
    </el-card>
    <div class="mt10 flex-row-center">
      <el-button type="primary" @click="sub">保存</el-button>
    </div>
    <!-- 权益列表 -->
    <equityList :showList="showList" :listIds="listIds" @close="showList = false" @getList="getList"></equityList>
    <!-- 优惠券列表 -->
    <couponList :showCouponList="showCouponList" :listCouponIds="listCouponIds" @couponClose="showCouponList = false" @getCouponList="getCouponList"></couponList>
  </div>
</template>
<script>
import equityList from './equityList';
import couponList from './couponList';
import { mapState } from 'vuex'
import { 
  getVIpConfig,
  openVip,
  addVIpConfig
} from '@/api/memberManage/memberCenter'
export default {
  props: ["getPost"],
  watch:{
    getPost(newvalue,oldValue){
      if(newvalue){
        console.log('请求了会员中心')
      }
    },
  },
  created(){
    this.getData()
  },
  data(){
    return{
      isOpenVip:'0',
      form:{
        openType:'1',
        name:'',
        isOpenGift:'1',
        sendCoupon:true
      },
      rules1:[{
        effectiveTime:'',
        unit:'',
        amount:0
      }],
      rules2:[{
        effectiveTime:'',
        unit:''
      }],
      showList:false,
      listIds:'',
      ids:[],
      equityList:[],

      showCouponList:false,
      listCouponIds:[],
      couponList:[]

    }
  },
  methods:{
    sub(){
      console.log('form',this.form)
      if(!this.form.name){
        this.$message.error('请填写名称');
        return;
      }
      if(this.form.openType == 1){
        let errArr = this.rules1.filter( item=> !item.effectiveTime || !item.unit)
        if(errArr.length > 0){
          this.$message.error('请完善规则');
          return;
        }
        this.form.rules = this.rules1
      }
      if(this.form.openType == 2){
        let errArr = this.rules2.filter( item=> !item.effectiveTime || !item.unit || !item.amount)
        if(errArr.length > 0){
          this.$message.error('请完善规则');
          return;
        }
        this.form.rules = this.rules2
      }
      console.log('789')
      this.form.ruleList = this.form.rules
      if(this.equityList.length == 0){
        this.$message.error('请至少添加一项权益');
        return;
      }
      let vipEquityId = []
      this.equityList.forEach(e => {
        let obj = {}
        obj.vipEquityId = e.id
        vipEquityId.push(obj)
      })
      this.form.equityList = vipEquityId
      if(this.form.isOpenGift == 1){
        this.form.isOpenCoupons = 1
      }
      if(this.form.isOpenGift == 2){
        if(!this.form.openRules){
          this.$message.error('请选择定时发放的日期');
          return;
        }
        this.form.isOpenCoupons = 2
        if(this.couponList.length == 0){
          this.$message.error('请选择要赠送的优惠券');
          return;
        }
        let errArr = this.couponList.filter( item=> !item.couponsCount)
        if(errArr.length > 0){
          this.$message.error('请完善列表中的赠送券数');
          return;
        }
        this.form.couponsList = this.couponList
      }
      this.form.shopId = this.shop.shopId
      this.form.createId = this.user.userId
      console.log('准备请求！',this.form)
      console.log('准备请求！',JSON.stringify(this.form))
      addVIpConfig(this.form).then(res=>{
        console.log('信息初始化',res)
        if(res.code == 200){
          this.$message({
            message: '保存成功',
            type: 'success'
          });
        }else{
          this.$message.error(res.message);
        }
      })
    },
    deleteEq(i){
      this.equityList.splice(i, 1)
      this.ids.splice(i, 1)
    },
    deleteCoupon(i){
      this.listCouponIds.splice(i, 1)
      this.couponList.splice(i, 1)
    },
    getData(){
      getVIpConfig(this.shop.shopId).then(res=>{
        console.log('获取会员中心',res)
        if(res.code == 200){
          this.isOpenVip = res.data.isOpenVip+''
          console.log('this.isOpenVip',this.isOpenVip)
          let form = {}
          if(res.data.vipConfig == null){
            form.name = ''
            form.openType = '1'
          }else{
            form = res.data.vipConfig
            form.openType = form.openType+''
            if(form.openType == 1) this.rules1 = JSON.parse(form.rules)
            if(form.openType == 2) this.rules2 = JSON.parse(form.rules)
            form.rules = JSON.parse(form.rules)
            form.isOpenGift = form.isOpenGift + ''
            form.isOpenCoupons = form.isOpenCoupons + ''
            if(form.isOpenCoupons == 2){
              form.sendCoupon = true;
            }else{
              form.sendCoupon = false
            }
          }
          console.log('form',form)
          this.form = form;
          this.equityList = res.data.equityList
          this.equityList.forEach( e => this.ids.push(e.id))
          this.couponList = res.data.couponsList
          this.couponList.forEach( e => this.listCouponIds.push(e.couponsId))
        }
      })
    },
    // 开启 、关闭会员功能
    changeStatus(){
      console.log('this.isOpenVip',this.isOpenVip)
      openVip({shopId:this.shop.shopId,isOpenVip:this.isOpenVip}).then(res=>{
        console.log('开启/关闭会员功能',res)
        if(res.code == 200){
          this.$message({
            message: res.message,
            type: 'success'
          });
        }else{
          this.$message.error(res.message);
        }
      })
    },
    // 添加规则
    addRule(){
      this.rules2.push({
        effectiveTime:'',
        unit:'',
        amount:'',
      })
    },
    // 删除规则
    deleteRule(i){
      this.rules2.splice(i, 1)
    },
    // 调取权限列表
    showListFun(){
      this.listIds = this.ids.join(',')
      this.showList = true
    },
    getList(e){
      console.log('父接受',e)
      e.forEach(e => {
        this.equityList.push(e)
        this.ids.push(e.id)
      })
      this.showList = false
    },
    getCouponList(e){
      console.log('父接受',e)
      e.forEach(e => {
        this.couponList.push(e)
        this.listCouponIds.push(e.couponsId)
      })
      this.showCouponList = false
    }
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },
  components:{
    equityList,
    couponList
  }
}
</script>
<style lang="less" scoped>
  .h3{
    font-weight: bold;
    font-size: 16px;
  }
  .h3word{
    margin-top: 4px;
    font-size: 12px;
    color: #909399;
  }
</style>