<template>
  <el-dialog title="优惠券列表" :visible.sync="showCouponList" width="70%" :append-to-body="true" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div class="flex-row-between">
        <div class="flex-row-start">
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <span class="infoFont" style="margin-left:15px;">优惠券不够？可以去</span>
          <el-button type="text" @click="newPage">新增优惠券</el-button>
        </div>
        <div>
          <el-button @click="getData" type="primary" size="small">查询</el-button>
          <el-button style="margin-right: 15px" @click="reset" size="small">重置</el-button>
        </div>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="优惠券名称：">
            <el-input style="width: 203px" v-model="searchList.couponName" clearable placeholder="优惠券名称" @input="change($event)"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container mt10" shadow="never">
      <el-table
        ref="multipleTable"
        :data="list"
        tooltip-effect="dark"
        style="width: 100%"
        @selection-change="handleSelectionChange">
        <el-table-column type="selection" width="55"></el-table-column>
        <el-table-column label="优惠券名称" prop="couponName"></el-table-column>
        <el-table-column label="优惠内容" width="240" prop="content"></el-table-column>
        <el-table-column label="限领次数" width="140">
          <template slot-scope="scope">{{ scope.row.limitNumber == 0 ? '不限制' : scope.row.limitNumber }}</template>
        </el-table-column>
      </el-table>
      <div style="padding:10px 0 20px 0;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5, 10, 15, 20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
      <el-button type="primary" @click="sub">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { getCouponsList } from '@/api/memberManage/memberCenter'
export default {
  props: ["showCouponList","listCouponIds"],
  watch:{
    showCouponList(newvalue,oldValue){
      if(newvalue){
        this.getData()
      }
    },
  },
  data(){
    return{
      searchList:{
        pageSize:5,
        pageNum:1
      },
      list:[],
      listTotal:0,
      selectImgArr:[]
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
  },
  methods:{
    // 分页
    handleSizeChange(val) {
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      this.searchList.pageNum = val
      this.getData();
    },
    newPage(){
      let details = this.$router.resolve({
        name: "优惠券",
      });
      window.open(details.href, "_blank")
    },
    change(){
      this.$forceUpdate()
    },
    sub(){
      if(this.multipleSelection.length == 0){
        this.$message.error('请选择优惠券后再试');
        return;
      }
      this.$emit('getCouponList',this.multipleSelection)
      this.$refs.multipleTable.clearSelection();
    },
    getData(){
      this.searchList.ids = this.listCouponIds.join(',') || ''
      getCouponsList(this.searchList).then(res=>{
        console.log('优惠券列表数据',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){
      this.searchList.couponName = ''
      this.getData()
    },
    close(){
      this.$emit('couponClose',true)
    },
    toggleSelection(rows) {
      if (rows) {
        rows.forEach(row => {
          this.$refs.multipleTable.toggleRowSelection(row);
        });
      } else {
        this.$refs.multipleTable.clearSelection();
      }
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },

}
</script>
<style lang="less" scoped>
.posBox{
  height: 80px;
  position: relative;
  .posWord{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);
    color: #fff;
    line-height: 80px;
    font-size: 32px;
    font-weight: bold;
  }
}
</style>