import request from '@/utils/request'
// 获取权益列表
export function getVipEquityList(data) {
  return request({
    url: `/sys/business/vipConfig/getVipEquityList`,
    method: 'post',
    data
  })
}
// 获取权益详情
export function getEquity(id) {
  return request({
    url: `/sys/business/vipConfig/getEquity?id=${id}`,
    method: 'post',
  })
}
// 新增权益
export function saveEquity(data) {
  return request({
    url: `/sys/business/vipConfig/saveEquity`,
    method: 'post',
    data
  })
}
// 删除权益
export function deleteEquity(id) {
  return request({
    url: `/sys/business/vipConfig/deleteEquity?id=${id}`,
    method: 'post',
  })
}
// 编辑权益
export function updateEquity(data) {
  return request({
    url: `/sys/business/vipConfig/updateEquity`,
    method: 'post',
    data
  })
}