<template>
  <el-dialog title="权限列表" :visible.sync="showList" width="70%" :append-to-body="true" :close-on-click-modal="false" :close-on-press-escape="false" :show-close="false">
    <!-- 筛选条件 -->
    <el-card class="filter-container" shadow="never">
      <div class="flex-row-between">
        <div class="flex-row-start">
          <i class="el-icon-search"></i>
          <span>筛选搜索</span>
          <span class="infoFont" style="margin-left:15px;">权益不够？可以去</span>
          <el-button type="text" @click="newPage">新增权益</el-button>
        </div>
        <div>
          <el-button @click="getData" type="primary" size="small">查询</el-button>
          <el-button style="margin-right: 15px" @click="reset" size="small">重置</el-button>
        </div>
      </div>
      <div style="margin-top: 15px">
        <el-form :inline="true" :model="searchList" size="small" label-width="140px">
          <el-form-item label="权益名称：">
            <el-input style="width: 203px" v-model="searchList.name" clearable placeholder="权益名称" @input="change($event)"></el-input>
          </el-form-item>
        </el-form>
      </div>
    </el-card>
    <el-card class="filter-container mt10" shadow="never">
      <el-row :gutter="20">
        <el-col :span="4" v-for="(item,index) in list" :key="index">
          <div class="tac mt10 posBox curP" @click="clickItem(item,index)">
            <el-image
              style="width: 50px; height: 50px"
              :src="item.equityUrl"
              fit="fill"></el-image>
            <div class="infoFont">{{item.equityName}}</div>
            <div class="posWord" v-show="showPos(item)">{{showNum(item)}}</div>
          </div>
        </el-col>
      </el-row>
      <div class="flex-row-end" style="padding-top:20px;">
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :page-sizes="[5,10,15,20]"
          :page-size="searchList.pageSize"
          layout="total, sizes, prev, pager, next, jumper"
          :total="listTotal">
        </el-pagination>
      </div>
    </el-card>
    <span slot="footer" class="dialog-footer">
      <el-button @click="close">取 消</el-button>
      <el-button type="primary" @click="sub">确 定</el-button>
    </span>
  </el-dialog>
</template>
<script>
import { mapState } from 'vuex'
import { 
  getVipEquityList ,
} from '@/api/memberManage/memberRoom'
export default {
  props: ["showList","listIds"],
  watch:{
    showList(newvalue,oldValue){
      if(newvalue){
        console.log('请求了权限列表')
        this.searchList.ids = this.listIds || ''
        this.getData()
      }
    },
  },
  data(){
    return{
      searchList:{
        pageSize:10,
        pageNum:1
      },
      list:[],
      listTotal:0,
      selectImgArr:[]
    }
  },
  created(){
    this.searchList.shopId = this.shop.shopId
    this.searchList.equityType = 1
  },
  methods:{
    // 分页
    handleSizeChange(val) {
      console.log(`每页 ${val} 条`);
      this.searchList.pageSize = val
      this.getData();
    },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
      this.searchList.pageNum = val
      this.getData();
    },
    newPage(){
      let details = this.$router.resolve({
        name: "权益库",
      });
      window.open(details.href, "_blank")
    },
    change(){
      this.$forceUpdate()
    },
    sub(){
      if(this.selectImgArr.length == 0){
        this.$message.error('请选择权益后再试');
        return;
      }
      this.$emit('getList',this.selectImgArr)
      this.selectImgArr = []
    },
    getData(){
      getVipEquityList(this.searchList).then(res=>{
        console.log('权益列表数据',res)
        if(res.code == 200){
          this.list = res.data.list
          this.listTotal = res.data.total
        }
      })
    },
    reset(){},
    close(){
      this.$emit('close',true)
    },
    clickItem(e){
      if(this.selectImgArr.findIndex(item=>item.id == e.id) === -1){
        this.selectImgArr.push(e)
      }else{
        this.selectImgArr.splice(this.selectImgArr.findIndex(item => item.id === e.id), 1)
      }
    },
    showNum(e){
      return (this.selectImgArr.findIndex(item=>item.id == e.id)) + 1;
    },
    // 图片判断是否展示不展示
    showPos(e){
      if(this.selectImgArr.findIndex(item=>item.id == e.id) === -1){
        return false;
      }else{
        return true;
      }
    },
  },
  computed: {
    ...mapState([
      'user',
      'shop'
    ])
  },

}
</script>
<style lang="less" scoped>
.posBox{
  height: 80px;
  position: relative;
  .posWord{
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0,0,0,0.7);
    color: #fff;
    line-height: 80px;
    font-size: 32px;
    font-weight: bold;
  }
}
</style>